import {
  AfterViewInit,
  Component,
  ElementRef,
  OnInit,
  Renderer2,
  ViewChild,
} from '@angular/core';

@Component({
  selector: 'app-paste-links',
  templateUrl: './paste-links.component.html',
  styleUrls: ['./paste-links.component.scss'],
})
export class PasteLinksComponent implements OnInit, AfterViewInit {
  @ViewChild('pasteLinksArea', { static: false }) pasteLinksArea?: ElementRef;
  links: any[] = [];
  valueForEditableTextArea: string = '';
  valueForTextBox: string = '';

  constructor(private renderer: Renderer2) {}

  ngOnInit(): void {}

  ngAfterViewInit(): void {
    if (this.pasteLinksArea != null) {
      this.renderer.listen(
        this.pasteLinksArea.nativeElement,
        'paste',
        (event) => {
          // handle the event
          console.log('Paste Event');
        }
      );
    }
  }

  onPaste(event: any): void {
    if (event.target && event.clipboardData) {
    }

    const links = event.clipboardData.getData('text');
    this.handleLinks(links);
  }

  handleLinks(links: string): void {
    const arrayLinks = links.split(/\r?\n/);

    const updatedlinks = arrayLinks.map((x: string) => {
      const filePath = x.replace(/(^")|("$)/g, '');
      const fileName = filePath.split(/[\\\/]/).pop();
      return { path: filePath, name: fileName };
    });

    this.links = this.links.concat(updatedlinks);

    if (this.pasteLinksArea) {
      this.pasteLinksArea.nativeElement.value = '';
    }
  }

  async callPasteEvent(event: any): Promise<void> {
    console.log('Paste Button Clicked');
    let clipboardText = '';
    if (navigator.clipboard) {
      clipboardText = await navigator.clipboard.readText();
    }

    this.handleLinks(clipboardText);
  }

  onFileDropped(event: any) {
    this.handleLinks(event);
  }

  deleteLink(index: number) {
    if (this.links[index].progress < 100) {
      console.log('Upload in progress.');
      return;
    }
    this.links.splice(index, 1);
  }

  downloadFileLink(link: string): string {
    const uncPath = link.replace(/\\/g, '/');
    const fileLink = `file:///` + uncPath;
    return fileLink;
  }

  async copyLink(link: string): Promise<void> {
    if (navigator.clipboard) {
      await navigator.clipboard.writeText(link);
    }
  }
}
