import { Component, OnInit } from '@angular/core';
import { RouterOutlet } from '@angular/router';
import { openCloseAnimation } from 'src/app/fade-in-transition';
import { DxHtmlEditorModule } from 'devextreme-angular';

@Component({
  selector: 'app-di-authoring',
  templateUrl: './di-authoring.component.html',
  styleUrls: ['./di-authoring.component.scss'],
  animations: [openCloseAnimation],
})
export class DiAuthoringComponent implements OnInit {
  constructor() {}

  pageButtons = [
    {
      name: 'Drag/Drop Links',
      link: 'drag-drop',
    },
    {
      name: 'HTML Editor',
      link: 'edit-text',
    },
    {
      name: 'Text Box',
      link: 'edit-textbox',
    },
    {
      name: 'Paste Links',
      link: 'paste-links',
    },
  ];

  ngOnInit(): void {}

  prepareRoute(outlet: RouterOutlet) {
    return (
      outlet &&
      outlet.activatedRouteData &&
      outlet.activatedRouteData[`animationState`]
    );
  }
}
