import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { openCloseAnimation } from 'src/app/fade-in-transition';

@Component({
  selector: 'app-drag-drop',
  templateUrl: './drag-drop.component.html',
  styleUrls: ['./drag-drop.component.scss'],
  animations: [openCloseAnimation],
})
export class DragDropComponent implements OnInit {
  @ViewChild('fileDropArea', { static: false }) fileDropArea?: ElementRef;
  files: any[] = [];

  constructor() {}

  ngOnInit(): void {}

  handleInputFiles(event: any): void {
    if (event.target && event.target.files) {
      console.log('Files added: ' + event.target.files);
    }

    const files = event.target.files;

    for (const item of files) {
      item.progress = 0;
      this.files.push(item);
    }
    if (this.fileDropArea) {
      this.fileDropArea.nativeElement.value = '';
    }

    this.simulateUploadingToServer(0);
  }

  simulateUploadingToServer(index: number) {
    setTimeout(() => {
      if (index === this.files.length) {
        return;
      } else {
        const progressInterval = setInterval(() => {
          if (this.files[index].progress === 100) {
            clearInterval(progressInterval);
            this.simulateUploadingToServer(index + 1);
          } else {
            this.files[index].progress += 5;
          }
        }, 200);
      }
    }, 1000);
  }

  onFileDropped(event: any) {
    this.handleInputFiles(event);
  }

  deleteFile(index: number) {
    if (this.files[index].progress < 100) {
      console.log('Upload in progress.');
      return;
    }
    this.files.splice(index, 1);
  }

  formatBytes(bytes: number, decimals = 2) {
    if (bytes === 0) {
      return '0 Bytes';
    }
    const k = 1024;
    const dm = decimals <= 0 ? 0 : decimals;
    const sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'];
    const i = Math.floor(Math.log(bytes) / Math.log(k));
    return parseFloat((bytes / Math.pow(k, i)).toFixed(dm)) + ' ' + sizes[i];
  }
}
