import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DiAuthoringComponent } from './di-authoring/di-authoring.component';
import { DragDropComponent } from './drag-drop/drag-drop.component';
import { EditTextComponent } from './edit-text/edit-text.component';
import { EditTextboxComponent } from './edit-textbox/edit-textbox.component';
import { PasteLinksComponent } from './paste-links/paste-links.component';

const routes: Routes = [
  {
    path: '',
    component: DiAuthoringComponent,
    data: { animationState: 'One' },
    children: [
      {
        path: '',
        component: DiAuthoringComponent,
        data: { animationState: 'One', pageTitle: 'DI-Authoring' },
      },
      {
        path: 'drag-drop',
        component: DragDropComponent,
        data: { animationState: 'One', pageTitle: 'Drag/Drop Links' },
      },
      {
        path: 'edit-text',
        component: EditTextComponent,
        data: { animationState: 'One', pageTitle: 'HTML Editor' },
      },
      {
        path: 'edit-textbox',
        component: EditTextboxComponent,
        data: { animationState: 'One', pageTitle: 'Edit Textbox' },
      },
      {
        path: 'paste-links',
        component: PasteLinksComponent,
        data: { animationState: 'One', pageTitle: 'Paste Links' },
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class DiAuthoringRoutingModule {}
